'use client';
import { useState, useEffect } from 'react';

export default function TargetSchoolsList({ onSchoolSelect }) {
  const [schools, setSchools] = useState([]);
  const [activeCategory, setActiveCategory] = useState('all');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchTargetSchools = async () => {
      try {
        const response = await fetch('/api/user/target-schools');
        if (!response.ok) throw new Error('Failed to fetch schools');
        const data = await response.json();
        setSchools(data);
      } catch (error) {
        console.error('Error:', error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchTargetSchools();
  }, []);

  const handleCategoryClick = (category) => {
    setActiveCategory(category);
    if (onSchoolSelect) {
      onSchoolSelect(category);
    }
  };

  if (isLoading) {
    return (
      <div className="portfolio-sidebar">
        <h3 className="category-title">志願學校系所</h3>
        <div className="loading">載入中...</div>
      </div>
    );
  }

  return (
    <div className="portfolio-sidebar">
      <h3 className="category-title">志願學校系所</h3>
      <ul className="category-list">
        <li 
          className={`category-item ${activeCategory === 'all' ? 'active' : ''}`}
          onClick={() => handleCategoryClick('all')}
        >
          <i className="fas fa-list"></i> 所有文件
        </li>
        
        {schools.map((school) => (
          <li 
            key={school.id}
            className={`category-item ${activeCategory === school.id ? 'active' : ''}`}
            onClick={() => handleCategoryClick(school.id)}
          >
            <i className="fas fa-university"></i>
            {` ${school.school.name}-${school.department.name}`}
          </li>
        ))}
      </ul>
    </div>
  );
} 